n: 0;\n  padding: 0;\n  border: 0;\n  font-size: 100%;\n  font: inherit;\n  vertical-align: baseline; }\n\n/* HTML5 display-role reset for older browsers */\n\narticle, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {\n  display: block; }\n\nbody {\n  line-height: 1; }\n\nol, ul {\n  list-style: none; }\n\nblockquote, q {\n  quotes: none; }\n\nblockquote {\n  &:before, &:after {\n    content: '';\n    content: none; } }\n\nq {\n  &:before, &:after {\n    content: '';\n    content: none; } }\n\ntable {\n  border-collapse: collapse;\n  border-spacing: 0; }\n","\n// Summoner Spells\n// TODO make dynamic\n@mixin summoner-spell($x, $y) {\n    height:48px;\n    width:48px;\n    background: url('https://ddragon.leagueoflegends.com/cdn/6.16.2/img/sprite/spell0.png') (-1 * $x) + px (-1 * $y) + px no-repeat;\n}\n\n.flash { @include summoner-spell(240, 0); }\n.cleanse { @include summoner-spell(48, 0); }\n.clairvoyance { @include summoner-spell(96, 0); }\n.ignite { @include summoner-spell(144, 0); }\n.barrier { @include summoner-spell(0, 0); }\n.exhaust { @include summoner-spell(192, 0); }\n.ghost { @include summoner-spell(288, 0); }\n.heal { @include summoner-spell(336, 0); }\n.clarity { @include summoner-spell(384, 0); }\n.smite { @include summoner-spell(48, 48); }\n.teleport { @include summoner-spell(144, 48); }\n.mark { @include summoner-spell(96, 48); }\n.poro-toss { @include summoner-spell(0, 48); }\n.to-the-king { @include summoner-spell(432, 48); }\n","body {\n  font-family: $font-stack;\n  color: $grey;\n  background-color: $background-color;\n\n  min-width: 320px;\n\n    &.fullscreen {\n        overflow: hidden;\n    }\n}\n\nh1 {\n    font-size: $h1 + px;\n}\n\nh2 {\n    font-size: $h1 * (.75) + px;\n}\n\nh3 {\n    font-size: $h1 * (.75) * (.75) + px;\n}\n\nh4 {\n    font-size: $h1 * (.75) * (.75) * (.75) + px;\n}\n\na {\n    @include resetLink();\n    cursor: pointer;\n    &:hover {\n        color: $primary-color;\n    }\n}\n\nem {\n  