size-padding: $icon-size - 2;\n            @include zoom($icon-size-padding / 48);\n        }\n    }\n\n    .icon {\n        display: inline-block;\n        height: $icon-size + px;\n        border-radius: 5px;\n        padding: 1px;\n        box-sizing: border-box;\n    }\n\n    .keystone-image {\n        display: inline-block;\n        margin: ($icon-size / 2) + px 0;\n    }\n\n    .items {\n        margin-left: 10px;\n        height: 100%;\n        width: ($icon-size * 4) + px;\n        display: inline-block;\n\n        .main-items {\n            float: left;\n            height: 100%;\n            width: ($icon-size * 3) + px;\n        }\n\n        .last-item img {\n            margin: ($icon-size / 2) + px 0;\n        }\n    }\n}\n","\n.twitch-autoplay-overlay {\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    background-color: rgba(0,0,0,0.2);\n\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n\n    color: $white;\n    a:hover {\n        color: $white;\n        text-decoration: underline;\n    }\n}\n\n.autoplay-loader{\n    height: 120px;\n    width: 120px;\n    transform: rotate(270deg);\n\n    .ring {\n        stroke-width: 10px;\n        fill: transparent;\n    }\n    .center {\n        fill: black;\n        fill-opacity: 0.8;\n    }\n    .progress {\n        stroke: $white;\n    }\n\n    .play-button {\n        fill: $white;\n        cursor: pointer;\n        &:hover {\n            fill: $red;\n        }\n    }\n}\n","\n.category-search {\n    width: 300px;\n    position: relative;\n\n    .input-container {\n        float: right;\n        height: 50px;\n        position: relative;\n        padding: (50 - 27) /2 + px 10px;\n        box-sizing: border-box;\n        width: 170px;\n        transition: width 0.3s ease-in-out;\n\n        &.focus {\n            width: calc(100% - 10px);\n        }\n\n        .input {\n            border-radius: 5px;\n            padding: 5px;\n            